import React, {useState} from "react";
import './allergens.css'





//allergyMenu display
export default function AllergyMenu ( {listAllergy, setAllergy}) {


    function handleClick(id){
        const newList = listAllergy.map((item) => {
            if(item.id === id) {
                const changed = {
                    ...item,
                    selected: !item.selected
                };
                return changed;
            }
            return item;
        });
        setAllergy(newList);
    }

    // function printAllergy(){
    //
    //     console.log(listAllergy)
    //
    // }

    return (
        <div className='allergy'>
            <div className='container'>
                <label>
                    <h2>Select allergies excluded from search</h2>
                    <button onClick={() => handleClick(0)}
                            className={listAllergy[0].selected ? 'toggle-off': 'toggle-on'} >Milk</button>
                    <button onClick={() => handleClick(1)}
                            className={listAllergy[1].selected ? 'toggle-off': 'toggle-on'} >Egg</button>
                    <button onClick={() => handleClick(2)}
                            className={listAllergy[2].selected ? 'toggle-off': 'toggle-on'} >Peanut</button>
                    <button onClick={() => handleClick(3)}
                            className={listAllergy[3].selected ? 'toggle-off': 'toggle-on'} >Treenut</button>
                    <button onClick={() => handleClick(4)}
                            className={listAllergy[4].selected ? 'toggle-off': 'toggle-on'} >Fish</button>
                    <button onClick={() => handleClick(5)}
                            className={listAllergy[5].selected ? 'toggle-off': 'toggle-on'} >Shellfish</button>
                    <button onClick={() => handleClick(6)}
                            className={listAllergy[6].selected ? 'toggle-off': 'toggle-on'} >Soy</button>
                    <br/>
                    <button onClick={() => handleClick(7)}
                            className={listAllergy[7].selected ? 'toggle-off': 'toggle-on'} >Wheat</button>
                    <button onClick={() => handleClick(8)}
                            className={listAllergy[8].selected ? 'toggle-off': 'toggle-on'} >Sesame</button>
                    <button onClick={() => handleClick(9)}
                            className={listAllergy[9].selected ? 'toggle-off': 'toggle-on'} >Alcohol</button>
                    <button onClick={() => handleClick(10)}
                            className={listAllergy[10].selected ? 'toggle-off': 'toggle-on'} >Gluten</button>

                    {/*<button onClick={() => printAllergy()} >print</button>*/}

                </label>
            </div>
        </div>
    )
}
