import React, {useState, useRef} from 'react';
import './App.css'
import Navbar from "./components/navBar";
import MealData from "./meals";


import AllergyMenu from "./components/allergens";
// import Switch from "react-switch";



function App() {
    const [search, setSearch] = useState('');
    const searchInput = useRef();

    const [listAllergy, setAllergy] = useState([
        {name: "dairy", id: 0, selected: false},
        {name: "Egg", id: 1, selected: false},
        {name: "peanut", id: 2, selected: false},
        {name: "treenut", id: 3, selected: false},
        {name: "fish", id: 4, selected: false},
        {name: "shellfish", id: 5, selected: false},
        {name: "soy", id: 6, selected: false},
        {name: "wheat", id: 7, selected: false},
        {name: "sesame", id: 8, selected: false},
        {name: "alcohol", id: 9, selected: false},
        {name: "gluten", id: 10, selected: false}
    ]);


    function printSearch(event){
        const find = searchInput.current.value;
        if (find === '') return
        if (event.key === 'Enter'){
            setSearch(searchInput.current.value);
            searchInput.current.value = null;
        }
    }

    return (
        <div className="App">
            <div className = "body">
                <Navbar>
                </Navbar>
                <AllergyMenu listAllergy={listAllergy} setAllergy={setAllergy} >
                </AllergyMenu>
                <div className= "search">
                    <h2>Meal Search</h2>
                    <input type="Text" ref={searchInput} onKeyPress={printSearch}/>
                    <MealData listAllergy={listAllergy} search={search}></MealData>
                </div>
            </div>
        </div>
    );
}

export default App;
